.container{
    justify-content: center;
    text-align: center;
}
.loginForms input{
    margin-bottom: 1vh;
    border-radius: 5px;
    border-width: 1px;
    height: 20px;
    border-style: none;
    background-color: lightgray;
}
.loginFormsError input{
    border-radius: 5px;
    border-width: 1px;
    --main-bg-color : rgb(176, 61, 61);
    border-color: var(--main-bg-color);
    height: 20px;
    border-style:solid;
    background-color: lightgray;
}
.registrationButton{
    width: 120px;
    height: 30px;
    font-size: medium;
    font-weight: 500;
    border-radius: 5px;
    background-color: lightgrey;
    border-width: 1px;
}