.chatWrapper{
    display: grid;
    /* width: 900px; */
    height:470px;
    max-height: 670px;
    grid-template-columns: 1fr 2fr;
    grid-gap: 2vh;
    grid-template-areas: 
    "chatList Messages"
    
    
}
.chatWindowWrapper{
    grid-area: Messages;
    padding-top: 1rem;
    display: grid;
    grid-template-rows: 40vh 20vh;
    border-style: solid ;
    border-width: 1px;
    border-color: lightgray;
    border-radius: 5px;
    grid-template-areas: 
    "messageArea"
    "input";
    width: 100%;
}
.messageArea{
    grid-area: messageArea;
    overflow: scroll;
}
.chatListWrapper{
    width: 100%;
    height: 100%;
    grid-area: chatList;
    border-style: solid ;
    border-width: 1px;
    border-color: lightgray;
    border-radius: 5px;
}
.chatListWrapper img{
    margin-top: 1rem;
    width: 60px;
    height: 60px;
    border-radius: 180px;
    
}
.userMiniPage span {
    display: inline-block;
    margin-bottom: 0.5rem;
    font-size: large;
}
.inputText{
    grid-area: input;

   
}

.messageInput{
    margin-bottom: 0%;
    padding-bottom: 0%;
    height: 5vh;
    width: 98%;
}
.currentUserMessage{
    text-align: right;

    
}
.currentUserMessage span{
    
    margin-bottom: 2rem;
    justify-content: right;
    margin-right: 1rem;
    background-color:  #82CA9C;
    margin-left: 50%;
    border-radius: 15px;
    padding: 2px 1rem 2px 1rem;
}
.message{
    text-align: left;

}
.message span{
    margin-left: 1rem;
    background-color:  #82CA9C;
    width:max-content;
    padding-right: 2rem;
    border-radius: 15px;
    padding: 2px 1rem 2px 1rem;
    margin-bottom: 2rem;
}
.chatWindowWrapper img{
    margin-top: 0.5rem;
    border-radius: 180px;
}
.sendMessage{
    width: 100%;
    background-color:  #82CA9C;
    border-style: none;
    border-radius: 3px;
    height: 2rem;
}
@media(max-width:530px){
    .chatListWrapper{

    }
    .chatWrapper{
        grid-template-columns: 50vh;
        grid-template-areas: 
        "Messages";
    }
}
@media(max-width:350){
    .chatWrapper{
        grid-template-columns: 1fr;
        grid-template-areas: 
        "Messages"
    }
}
.hr{
    height: 1px;
    width: 100%;
    background-color: lightgrey;
}